import React, { Component } from "react";

import Card from "../../ui/Card";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import PlayIcon from "@material-ui/icons/PlayCircleFilled";
import PauseIcon from "@material-ui/icons/PauseCircleFilled";
import { withRouter } from "react-router";
import { getCityName } from "utils/path";

class Time extends Component {
  state = {
    interval: undefined
  };

  componentDidMount() {
    this.setState({ interval: setInterval(this.tickTock, 1000) });
  }

  handleClick = () => {
    clearInterval(this.state.interval);
  };

  tickTock = () => {
    this.props.dispatchTickTime(getCityName(this.props.location.pathname));
  };

  render() {
    return (
      <Card name="Time">
        Year: {this.props.time.year} Month: {this.props.time.month} Day:{
          this.props.time.day
        }
        <Button size="large" color="primary" onClick={this.handleClick}>
          Pause
        </Button>
      </Card>
    );
  }
}

export default withRouter(Time);



